<template>
  <div class="bugo-list">
    <div @click="obituaryReg2(obituary.id)">
      <div class="name mb-1">
        <h4 v-if="obituary.obituaryAgeYn && obituary.obituarySexYn">
          故 {{ obituary.obituaryName }}
        </h4>
        <h4 v-else>
          故 {{ obituary.obituaryName }}
          <p>
            &nbsp; (
            <span v-show="obituary.obituaryAge && !obituary.obituaryAgeYn">
              {{ obituary.obituaryAge }}세
            </span>
            <span v-show="!obituary.obituaryAgeYn && !obituary.obituarySexYn"
              >/</span
            >
            <span v-show="!obituary.obituarySexYn">
              {{ obituary.obituarySex === "M" ? "남" : "여" }}
            </span>
            )
          </p>
        </h4>
      </div>
      <div class="mb-1">
        <h5 v-if="memberAttr === 'TYPE-0'">
          {{
            obituary.obituaryMortuary !== null
              ? obituary.obituaryMortuary.mortuaryNumber
              : "빈소없음"
          }}
        </h5>
        <h5 v-if="memberAttr === 'TYPE-1'">
          {{
            obituary.obituaryAddrName +
              " " +
              (obituary.obituaryMortuaryName !== null
                ? obituary.obituaryMortuaryName
                : "")
          }}
        </h5>
      </div>
      <div class="title-content__wraps mb-1">
        <h6>상주</h6>
        <p
          v-if="
            !obituary.obituaryObituaryMember ||
              obituary.obituaryObituaryMember.length === 0
          "
        >
          없음
        </p>

        <p v-else>
          {{ memberLeader.obituaryMemberName }}
          <span v-if="obituary.obituaryObituaryMember.length - 1 !== 0">
            외
            {{ obituary.obituaryObituaryMember.length - 1 }}명
          </span>
        </p>
      </div>
      <div class="title-content__wraps">
        <h6>발인</h6>
        <p v-if="obituary.obituaryEndDate">
          {{ KOREAYYYYMMDDEEHHMMDate(obituary.obituaryEndDate) }}
        </p>
        <p v-else>미정</p>
      </div>
    </div>
    <!-- <span v-if="obituary.endFlag === 'Y'">장례중</span>
    <span v-if="obituary.endFlag === 'N'" class="vacancy">발인</span> -->

    <div v-if="page === 'movie'" class="button__wrap">
      <button
        style="color: #5c4d46; background: #fff"
        @click="sendMovieLink(obituary)"
      >
        추모영상 제작 링크 보내기
      </button>
    </div>

    <div v-else-if="obituary.endFlag === 'Y'" class="button__wrap">
      <button @click="obituaryView(obituary)">미리보기</button>
      <button @click="obituaryUpdate(obituary.id)">부고수정</button>
    </div>
    <div v-else class="button__wrap">
      <button @click="obituaryView(obituary)">추모관</button>
      <button
        @click="obituaryReply(obituary.obituaryObituaryMember, obituary.id)"
      >
        답조글
      </button>
    </div>

    <div class="menu">
      <v-menu bottom left>
        <template #activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item v-for="(item, i) in menus" :key="i">
            <button @click="changeCorp">{{ item.title }}</button>
          </v-list-item>
        </v-list>
      </v-menu>
    </div>
  </div>
</template>

<script>
import dateFomat from "@/mixin/dateFomat";
import { REQUEST_ENCODE } from "@/store/common";
export default {
  mixins: [dateFomat],
  props: {
    page: {
      type: String,
      default: String
    },
    obituary: {
      type: Object,
      default: Object
    },
    memberAttr: {
      type: String,
      default: String
    }
  },
  data() {
    return {
      menus: [{ title: "상조변경" }]
    };
  },
  computed: {
    memberLeader() {
      const leader = this.obituary.obituaryObituaryMember.filter(
        v => v.obituaryMemberLeader === true
      );

      if (leader?.length) return leader[0];

      return this.obituary.obituaryObituaryMember[0];
    }
  },

  methods: {
    obituaryUpdate(id) {
      debugger;
      this.$router.push({
        path: "/obituaryUpdate",
        query: {
          id: id
        }
      });
    },

    obituaryView(obituary) {
      this.$store.dispatch("REQUEST_ENCODE", obituary.id).then(data => {
        if (data.result) {
          // window.open(
          //   `/obituary?id=${data.str}`,
          //   "부고장",
          //   "width=800,height=1500"
          // );
          window.open(
            `${process.env.VUE_APP_OBITUARY_BASEURL}/obituary?id=${data.str}`,
            "부고장",
            "width=800,height=1500"
          );
        }
      });
    },

    obituaryReply(memberList, id) {
      if (
        memberList !== null &&
        memberList !== undefined &&
        memberList.length > 0
      ) {
        this.mobileSmsType = "reply";
        this.obituaryUserItems = memberList;
        this.mobileUserPopupFlag = true;
        this.obituaryId = id;
      } else {
        const url = `${process.env.VUE_APP_OBITUARY_BASEURL}/api/obituary/reply?id=${id}`;
        const smsMessage =
          "[답례인사 안내]\n\n상주님, 이번 상중에 조의를 표한 조문객분들께 감사 인사를 보내시도록 답례 인사 예시문을 보내드립니다\n\n 내용일 확인, 수정하신 후 답례글을 보내세요\n\n아래 url 또는 아래 버튼을 누르시면 확인 하실 수  있습니다.\n\n [답례인사 예시문]\n" +
          url +
          "\n\n다시한번 고인의 명복을 빕니다";

        let realSmsMessage = smsMessage.replace(/(\n|\r\n)/g, "%0a");

        realSmsMessage = smsMessage;
        const varUA = navigator.userAgent.toLowerCase(); // userAgent 값 얻기

        let phoneType = "";
        if (varUA.includes("android")) {
          // 안드로이드
          phoneType = "android";
        } else if (
          varUA.includes("iphone") ||
          varUA.includes("ipad") ||
          varUA.includes("ipod")
        ) {
          // IOS
          phoneType = "ios";
        } else {
          // 아이폰, 안드로이드 외
          phoneType = "other";
        }

        location.href =
          "sms:" +
          (phoneType == "ios" ? "&" : "?") +
          "body=" +
          encodeURIComponent(realSmsMessage);
      }
    },

    changeCorp() {
      // this.$emit("changeCorp");
      alert("서비스 준비중 입니다");
    },

    sendMovieLink(obituary) {
      this.$router.push(`/movie-detail?id=${obituary.id}`);
    },
    obituaryReg2(id) {
      if (this.$route.path === "/obituary-list") {
        this.$emit("obituaryReg2", id);
      }
    }
  }
};
</script>

