<template>
  <div id="obituary-list">
    <MobileHeader
      title="부고 관리"
      class="mb-0"
      right-icon="mdi-plus"
      @close="closeObituaryList"
      @clickRightIcon="move('/obituaryReg')"
      style="border-bottom: none"
    />
    <div id="search-area">
      <div class="search-select">
        <v-select
          v-model="searchType"
          :items="searchTypes"
          item-text="name"
          item-value="value"
          outlined
          color="main"
          hide-details
          dense
        ></v-select>
      </div>
      <div class="search-input">
        <v-text-field
          v-model="searchTxt"
          type="text"
          placeholder="검색어를 입력해주세요"
          class="inputStyle"
          outlined
          color="main"
          hide-details
          dense
          @keyup.enter="requestSearch()"
        >
          <template #append>
            <v-icon
              style="background: none"
              color="#988A84"
              @click="requestSearch()"
              >mdi-magnify</v-icon
            >
          </template>
        </v-text-field>
      </div>
    </div>
    <nav class="bugo-tab__nav my-2">
      <ul>
        <li
          :class="{ active: endFlag === 'all' }"
          @click="requestSearch('all')"
        >
          전체
        </li>
        <li
          :class="{ active: endFlag === 'ing' }"
          @click="requestSearch('ing')"
        >
          장례식중
        </li>
        <li
          :class="{ active: endFlag === 'end' }"
          @click="requestSearch('end')"
        >
          발인
        </li>
      </ul>
    </nav>

    <!-- //search area -->

    <!-- content -->
    <div class="card__wraps">
      <template v-for="n in 4">
        <v-skeleton-loader
          v-if="getObituaryList.length === 0"
          class="mt-6"
          :key="n"
          type="article, actions"
          style="border: 1px solid #dddddd; border-radius: 6px"
        ></v-skeleton-loader>
      </template>
      <ObituaryListCard
        v-for="(obituary, index) in getObituaryList"
        :key="index"
        :obituary="obituary"
        :member-attr="memberInfo.memberAttr"
        @changeCorp="corpChangeFlag = !corpChangeFlag"
        @obituaryReg2="obituaryReg2"
      />
    </div>

    <div class="paging">
      <button
        v-if="startPage !== 1"
        class="prev"
        @click="onPageChange(startPage - 1)"
      >
        이전
      </button>
      <a
        v-for="(pageIdx, index) in pages()"
        :key="index"
        :class="{ on: pageIdx === page + 1 }"
        @click="onPageChange(pageIdx)"
        >{{ pageIdx }}</a
      >
      <button
        v-if="getObituaryPageTotal > endPage"
        class="next"
        @click="onPageChange(endPage + 1)"
      >
        다음
      </button>
    </div>
    <corp-change-modal v-if="corpChangeFlag" @cancel="corpChangeFlag = false" />
    <obituary-reply-modal v-if="replyFlag" @cancel="replyFlag = false" />
  </div>
</template>

<script>
import { setTimeout } from "timers";
import { mapGetters } from "vuex";

import CorpChangeModal from "@/components/common/modal/CorpChangeModal.vue";
import ObituaryReplyModal from "@/components/common/modal/ObituaryReplyModal.vue";
import ObituaryListCard from "@/components/obituary/ObituaryListCard.vue";

import { REQUEST_OBITUARY_LIST } from "@/store/obituary";
import dateFomat from "@/mixin/dateFomat";
import MobileHeader from "@/components/common/MobileHeader.vue";

export default {
  name: "MobileAdminObituaryList",
  components: {
    ObituaryListCard,
    CorpChangeModal,
    ObituaryReplyModal,
    MobileHeader
  },
  mixins: [dateFomat],
  data() {
    return {
      searchTypes: [
        { name: "고인명", value: "name" },
        { name: "상주명", value: "master" },
        { name: "상주전화번호", value: "phone" }
      ],
      navs: [
        { name: "전체", value: "all" },
        { name: "장례식중", value: "ing" },
        { name: "발인", value: "end" }
      ],
      corpNavs: [
        { name: "전체", value: "all" },
        { name: "장례식중", value: "ing" },
        { name: "발인", value: "end" },
        { name: "비고" },
        { name: "분향소 현황판" }
      ],
      obituary: {
        id: null,
        obituaryName: null,
        obituaryAge: null,
        obituaryAgeYn: null,
        obituarySex: null,
        obituarySexYn: null,
        obituaryMortuary: null,
        obituaryCode: null,
        obituaryDieYn: null,
        obituaryDieDate: null,
        obituaryEnterDate: null,
        obituaryCasketDate: null,
        obituaryCasketYn: null,
        obituaryReligionDate: null,
        obituaryReligionYn: null,
        obituaryEndDate: null,
        obituaryPlace: null,
        obituaryText: null,
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 8,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },
      changeType: this.$route.query.changeType
        ? this.$route.query.changeType
        : "",
      endFlag: this.$route.query.endFlag ? this.$route.query.endFlag : "ing",
      searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : "",
      searchType: this.$route.query.searchType
        ? this.$route.query.searchType
        : "name",
      pages: function() {
        const list = [];
        for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
        return list;
      },

      corpChangeFlag: false,
      replyFlag: false
    };
  },
  computed: {
    ...mapGetters([
      "getMainNoticeList",
      "getObituaryList",
      "getObituaryPage",
      "getObituaryPageTotal",
      "getObituaryTotalElements",
      "getObituaryCount",
      "getObituaryReadCount",
      "memberInfo"
    ]),
    startPage() {
      return parseInt((this.getObituaryPage - 1) / 5) * 5 + 1;
    },
    endPage() {
      return (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5 <
        this.getObituaryPageTotal
        ? (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5
        : this.getObituaryPageTotal;
    }
  },
  created() {
    this.$store.dispatch("REQUEST_MAIN");
    this.requestGetObituaryList();
  },
  mounted() {
    //let recaptchaScript = document.createElement('script')
    //recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
    //document.head.appendChild(recaptchaScript)
  },
  beforeDestroy() {
    this.$store.commit("OBITUARY_INIT");
  },
  methods: {
    closeObituaryList() {
      this.$router.push("/");
    },

    move(path) {
      this.$router.push(path);
    },

    changeTypeClick() {
      window.open(
        `${process.env.VUE_APP_POPUP_BASEURL}/popupManagerBoard`,
        "분향소현황판",
        "height=" + screen.height + ",width=" + screen.width + "fullscreen=yes"
      );
    },
    changePopupBigoClick() {
      this.$store.dispatch("REQUEST_MANAGER_DISPLAY_POPUP");
    },

    obituaryReg() {
      this.$router.push("/obituaryReg");
    },

    requestGetObituaryList() {
      const parameter = {
        obituary: this.obituary,
        endFlag: this.endFlag,
        searchTxt: this.searchTxt,
        searchType: this.searchType
      };
      this.$store.dispatch("REQUEST_OBITUARY_LIST", parameter);
    },
    requestSearch(flag) {
      this.changeType = "";
      if (flag !== null && flag !== undefined) {
        this.endFlag = flag;
      }
      this.requestObituaryList();
    },
    onPageChange(val) {
      const param = {
        page: val
      };
      this.requestObituaryList(param);
    },
    requestObituaryList(param) {
      let currentPage = 0;
      if (
        param !== undefined &&
        param !== null &&
        param.page !== undefined &&
        param.page !== null
      ) {
        currentPage = param.page - 1;
      }
      this.$router.push({
        path: "/obituary-list",
        query: {
          changeType: this.changeType,
          endFlag: this.endFlag,
          searchTxt: this.searchTxt,
          searchType: this.searchType,
          page: currentPage,
          size: this.obituary.page.size,
          sort: this.obituary.page.sort
        }
      });
    },
    obituaryDelete(obituary) {
      if (obituary.endFlag === "Y") {
        if (
          confirm(
            "해당 부고는 현재 장례를 진행중에 있습니다. 발인 시키겠습니까?"
          )
        ) {
          const parameter = {
            obituary: {
              id: obituary.id,
              page: this.obituary.page
            },
            endFlag: this.endFlag,
            searchTxt: this.searchTxt,
            searchType: this.searchType
          };
          this.$store.dispatch("REQUEST_DO_ENDTIME_OBITUARY", parameter);
        }
      } else {
        if (confirm("해당 부고를 삭제 시키겠습니까?")) {
          const parameter = {
            obituary: {
              id: obituary.id,
              page: this.obituary.page
            },
            endFlag: this.endFlag,
            searchTxt: this.searchTxt,
            searchType: this.searchType
          };
          this.$store.dispatch("REQUEST_DELETE_OBITUARY", parameter);
        }
      }
    },
    obituaryReg2(id) {
      debugger;
      this.$router.push({
        path: "/obituaryReg2",
        query: {
          id: id
        }
      });
    }
  }
};
</script>

<style>
#obituary-list .bugo-tab__nav {
  height: 53px;

  display: flex;
  align-items: center;
  justify-content: start;

  padding: 0px 20px;
}
#obituary-list .bugo-tab__nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;

  padding-left: 0px;
}
#obituary-list .bugo-tab__nav ul li {
  padding: 10px;

  font-family: var(--noto3);
  font-weight: 400;
  font-size: 16px;
  color: #212121;
}
#obituary-list .bugo-tab__nav ul li.active {
  font-weight: 600;
  font-size: 16px;

  border-bottom: 3px solid #534641;
}
#obituary-list .card__wraps {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
#obituary-list .paging {
  display: flex;
  justify-content: center;
  gap: 16px;

  padding: 16px 0px;

  font-family: var(--noto3);
  font-size: 18px;
}
#obituary-list .paging a {
  color: #777;
}
#obituary-list .paging a.on {
  color: #222;
  font-weight: 600;
}
</style>
